import { AfterViewInit, Component, ElementRef, Input, OnChanges, SimpleChanges, ViewChild } from '@angular/core';
import { CommonModule } from '@angular/common';
import { URLS } from '@enums/urls';
import { ImageComponent } from '@ApiClient/components/image/image.component';
import { RouterLink } from '@angular/router';
import { TranslateModule, TranslateService } from '@ngx-translate/core';
import { IPostFull } from '@ApiClient/backend/Post/post';
import { ClickedOutsideModule } from '../../directives/clickedOutside.module';
import { SkeletonModule } from 'primeng/skeleton';
import { Api } from '@ApiClient/backend/api';
import { MessageService } from 'primeng/api';
import { IReactionUsers } from '@ApiClient/backend/Extra/reaction';

@Component({
  selector: 'app-post-footer',
  templateUrl: 'post-footer.component.html',
  styleUrls: ['post-footer.component.scss'],
  standalone: true,
  imports: [CommonModule, ImageComponent, RouterLink, ClickedOutsideModule, TranslateModule, SkeletonModule],
})
export class PostFooterComponent implements OnChanges, AfterViewInit {
  @Input() post!: IPostFull | null;
  @ViewChild('textContainer') textContainer!: ElementRef;
  showMoreText = false;
  totalComments = 0;
  userReactions: IReactionUsers[] = [{ reaction: 'Like', count: BigInt(0) }];
  title = '';
  description = '';
  postId = '';
  protected readonly URLS = URLS;

  constructor(
    private toast: MessageService,
    private translate: TranslateService,
  ) {}

  ngAfterViewInit() {
    const textElement = this.textContainer.nativeElement;
    this.showMoreText = textElement.scrollHeight > textElement.clientHeight;
  }
  ngOnChanges(changes: SimpleChanges) {
    if ('post' in changes && this.post !== null) {
      this.totalComments = this.post.totalComments;
      this.userReactions = this.post.reactions.length > 0 ? this.post.reactions : this.userReactions;
      this.title = this.post.languages[0].title;
      this.description = this.post.languages[0]?.description ?? '';
      this.postId = this.post.id;
    }
  }

  toggleShowMore() {
    this.textContainer.nativeElement.classList.toggle('show-all');
  }

  async copyLink(id: string) {
    await this.copy(`${Api.AppUrl}/${this.translate.currentLang}/${URLS.POST}/${id}`);
  }

  async copy(msg: string) {
    try {
      await navigator.clipboard.writeText(msg);
      this.toast.add({
        severity: 'success',
        summary: this.translate.instant('SUCCESS.TITLE'),
        detail: this.translate.instant('SUCCESS.URL_COPY'),
      });
    } catch {
      this.toast.add({ severity: 'error', summary: 'Error', detail: this.translate.instant('ERROR.URL_COPY') });
    }
  }
}
