import { Component, Input, OnChanges, OnInit, SimpleChanges } from '@angular/core';
import { CommonModule } from '@angular/common';
import { URLS } from '@enums/urls';
import { ImageComponent } from '@ApiClient/components/image/image.component';
import { RouterLink } from '@angular/router';
import { Observable, startWith } from 'rxjs';
import { map } from 'rxjs/operators';
import { TranslateModule, TranslateService } from '@ngx-translate/core';
import { IPostFull } from '@ApiClient/backend/Post/post';
import { ClickedOutsideModule } from '../../directives/clickedOutside.module';
import { BreakpointObserver } from '@angular/cdk/layout';
import { SkeletonModule } from 'primeng/skeleton';

@Component({
  selector: 'app-post-header',
  templateUrl: 'post-header.component.html',
  styleUrls: ['post-header.component.scss'],
  standalone: true,
  imports: [CommonModule, ImageComponent, RouterLink, ClickedOutsideModule, TranslateModule, SkeletonModule],
})
export class PostHeaderComponent implements OnInit, OnChanges {
  @Input() post!: IPostFull | null;
  showMoreOptionTab: boolean = false;
  isMobileScreen: boolean = false;
  locale$: Observable<string> = this.translate.onLangChange.pipe(
    startWith({ lang: this.translate.currentLang }),
    map((event) => event.lang),
  );
  creatorId = '';
  userName = '';
  pictureId = '';
  nicknameUrl = '';

  protected readonly URLS = URLS;

  constructor(
    private translate: TranslateService,
    private breakpointObserver: BreakpointObserver,
  ) {}
  ngOnInit() {
    this.breakpointObserver.observe(['(max-width: 768px)']).subscribe((res) => {
      this.isMobileScreen = res.matches;
    });
  }

  ngOnChanges(changes: SimpleChanges) {
    if ('post' in changes && this.post !== null) {
      this.creatorId = this.post.userCreatorId;
      this.userName = this.post.userCreator.userProfile?.name ?? '';
      this.pictureId = this.post.userCreator?.pictureId ?? '';
      this.nicknameUrl = this.post.userCreator.userNickname?.nicknameUrl ?? '';
    }
  }

  toggleMoreOption(event?: MouseEvent): void {
    if (event) {
      event.stopPropagation();
    }

    this.showMoreOptionTab = !this.showMoreOptionTab;
  }

  clickedOutside() {
    this.showMoreOptionTab = false;
  }
}
